@import url("https://fonts.googleapis.com/css?family=PT+Serif&display=swap");
@font-face {
  font-family: muchosanslight;
  src: url(../../../../assets/Mucho_Sans_Light.otf);
}



[class*="close-"] {
  color: #777;
  font: 14px/100% arial, sans-serif;
  text-decoration: none;
  text-shadow: 0 1px 0 #fff;
  background-color: transparent;
  margin: 0;
  padding: 0;

}
.close-thik:after {
  content: "✖";
}

div.model {
  margin: 0 auto;
  padding: 0;
  position: relative;
  left: 0px;
  width: 100%;
}

img#background {
  width: 100%;
  height: auto;
  margin: 0 auto;
  display: block;
}

img#mobile {
  display: block;
  margin: 0 auto;

  /* width: 70%; */
  height: auto;
}

.hover-question {
  position: absolute;
  padding: 0;
  -webkit-box-shadow: 0px 0px 23px -1px rgba(0, 0, 0, 0.45);
  -moz-box-shadow: 0px 0px 23px -1px rgba(0, 0, 0, 0.45);
  box-shadow: 0px 0px 23px -1px rgba(0, 0, 0, 0.45);
  display: block;
  z-index: 10;
  display: flex;
  flex-direction: column;
}
.close-thik {
  align-self: flex-start;
  width: 20px!important
}

.hover-text,
.onhover-quiz-bttn,
.onhover-findoutMore-bttn {
  position: absolute;
  font-family: muchosanslight;
}

#building-line-1 {
  position: relative;
  height: 50px;
  background: black;
  top: 270px;
  width: 200px;
}

.not-hovered-question {
  display: none;
}

.check {
  position: relative;
  top: 10px;
  left: 10px;
}

.hover-text {
  position: relative;
  font-family: "PT Serif", serif;
}

.loading {
  position: relative;
  top: 300px;
  left: 700px;
  background: coral;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.onhover-quiz-bttn,
.onhover-findoutMore-bttn {
  margin: 10px 0px 10px;
}

.onhover-quiz-bttn {
  position: relative;
 
  margin: 10px 0px 10px;
}

.onhover-findoutMore-bttn {
  position: relative;
}

.bttn-text {
  position: relative;
}
.hover-text {
  text-align: center;
}

